const targetOrigins =
    process.env.NODE_ENV !== 'development'
        ? ['myadfinder.com']
        : ['myadfinder.com', 'http://localhost:3000'];
window.addEventListener('message', (event) => {
    if (
        event.source === window &&
        event.data &&
        event.data.direction === 'fps' &&
        targetOrigins.some((targetOrigin) => event.origin.indexOf(targetOrigin) > -1)
    ) {
        chrome.runtime.sendMessage({ type: 'get-state' }, (response) => {
            window.postMessage(
                {
                    direction: 'fcs',
                    message: response,
                },
                event.origin,
            );
        });
    }
});
